/**************************************时间格式化处理************************************/
function dateFtt(fmt,date)   
{ //author: meizz   
  var o = {   
	"M+" : date.getMonth()+1,                 //月份   
	"d+" : date.getDate(),                    //日   
	"h+" : date.getHours(),                   //小时   
	"m+" : date.getMinutes(),                 //分   
	"s+" : date.getSeconds(),                 //秒   
	"q+" : Math.floor((date.getMonth()+3)/3), //季度   
	"S"  : date.getMilliseconds()             //毫秒   
  };   
  if(/(y+)/.test(fmt))   
	fmt=fmt.replace(RegExp.$1, (date.getFullYear()+"").substr(4 - RegExp.$1.length));   
  for(var k in o)   
	if(new RegExp("("+ k +")").test(fmt))   
  fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));   
  return fmt;   
}
import mMap from '@/components/m-map'
import mOrderPro from '@/components/m-order-pro'
import ruiDatePicker from '@/components/rattenking-dtpicker/rattenking-dtpicker.vue';
export default {
	components:{
		mMap,
		mOrderPro,
		ruiDatePicker
	},
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			payLoading:false,
			storeid:'',
			storeData:{},
			distance:'',
			today:dateFtt("yyyy-MM-dd hh:mm",new Date()),
			aboutPickingTime:undefined,//预约时间
			reserveTel:"",// 预约手机号
			paytype:"wx",//支付方式
			type:"",//是否是拼团订单
			latitude: 39.909,
			longitude: 116.39742,
			
			shopCarList:[],
			totalCount:0,
			//结算start
			totalPrice:"",
			couponPrice:"",
			yhPrice:"",
			discount:0,
			payPrice:0,
			couponId:0,
			// 结算end
			// 优惠券start
			haveTokenCard:false,
			// 优惠券end
			date: "选择日期",
			time: "选择时间"
		};
	},
	methods:{ 
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		},
		 bindDateChange: function(e) {
			this.date = e.target.value
		},
		bindTimeChange: function(e) {
			this.time = e.target.value
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		// 选择优惠券
		choseTokenFn(){
			uni.navigateTo({
				url:"/pages/order/tokens?storeid="+this.storeid
			})
		},
		paytypeFn(type){
			this.paytype=type;
		},
		// 支付数据
		getData(option){
			let _this= this;
			if(option){
				let where = '';
				if(option.where){
					where = option.where;
				}
				let proUrlData=decodeURI(option.proUrlData);
				_this.shopCarList=JSON.parse(proUrlData)['proUrlData'];
				if(where=='orderPage'){
					_this.shopCarList=_this.shopCarList.map(item=>{
						// console.log(item)
						return {...item,...{pictureUrl:item['pictures'][0]['pictureUrl']}}
					})
					console.log(_this.shopCarList);
				}
				
			}
			_this.orderInit()
		},
		// 优惠券
		tokenCard(){
			let _this=this;

			_this.$apis.postUsableCoupons({
				storeId:_this.storeid,
				start:1,
				length:1000,
			}).then(res=>{
				
				if(res.data.coupons&&res.data.coupons.length>0){
					_this.haveTokenCard=true
				}
			})
		},
		//生成订单
		orderInit(){
			let _this=this;
			let products=_this.shopCarList.map(item=>{return {
				productId:item.productId||item.id,
				cou:item.buyCount,
			}})
			let sendData = {
				storeId:_this.storeid,
				totalCount:_this.totalCount,
				type:_this.type,
				couponId:_this.couponId,
				products:products, 
			}
			
			_this.$apis.postCalOrderPrice(sendData).then(res=>{
				if(res.code=='1'){
					let data= res.data;
					_this.totalPrice=data.totalPrice;
					_this.couponPrice=data.couponPrice;
					_this.yhPrice=data.yhPrice;
					_this.discount=data.discount;
					_this.payPrice=data.payPrice;
				}
			})
		},
		// 立即支付
		payFn(){
			let _this=this;
			this.aboutPickingTime = this.date +" "+this.time;
			if(this.date == '选择日期' || this.time == '选择时间'){
				uni.showToast({
					title:  "请选择取货日期",
					icon: "none"
				});
				return false
			}
			_this.payLoading=true;
			let products=_this.shopCarList.map(item=>{return {
				productId:item.productId||item.id,
				cou:item.buyCount,
			}})
			let sendData ={
				storeId:_this.storeid,//门店id
				totalCount:_this.totalCount,//商品总数量
				type:_this.type,//标识是普通下单还是拼团下单 1：普通 2：拼团
				couponId:_this.couponId,//优惠券id,
				aboutPickingTime:_this.aboutPickingTime?_this.aboutPickingTime+":00":"",//预计取货时间 yyyy-MM-dd hh:mm
				reserveTel:_this.reserveTel,//预留手机号
				products:products//商品数组对象
			}
			if(_this.couponId){
				sendData['couponId']=this.couponId;
			}
			if(_this.outTradeNo){
				sendData['outTradeNo']=this.outTradeNo; //订单id，第一次下单不需要，待支付订单支付时需要传入
			}
			_this.$apis.postWxpay(sendData).then(res=>{
				_this.payLoading=false;
				let data = res.data;
				if(!data.paySign){
					this.paySuccess();
					return
				}
				// 调起支付
				let _package = data.prepay_id;
				let paydata = {
					provider: 'wxpay',
					timeStamp: data.timeStamp+'',
					nonceStr: data.nonceStr,
					package: data.package,
					signType: data.signType,
					paySign: data.paySign,
				}
				this.payLoading=true;
				uni.requestPayment({
					...paydata,
					success: function(res) {
						_this.payLoading=false;
						_this.paySuccess()
					},
					fail: function(err) {
						_this.payLoading=false;
						//取消支付
						uni.setStorageSync('orderTab', 2);
						uni.switchTab({  
							url: '/pages/tabBar/order'  
						}); 
						 // _this.clearShopcar()
					},
					complete() {
						_this.payLoading=false;
					}
				});
				
			}).catch(err=>{
				_this.payLoading=false;
			})
		},
		//支付成功
		paySuccess(){
			uni.showModal({
				title: '支付成功',
				content: '可在我的订单中查看详情',
				showCancel:false,
				confirmText:'查看订单',
				success: function (res) {
					if (res.confirm) {
						uni.setStorageSync('orderTab', 1);
						uni.switchTab({  
							url: '/pages/tabBar/order'  
						});
					} 
					else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});	
		},
		// 清空购物车
		clearShopcar(){
			let _this = this;
			_this.$apis.clearShopCar({
				// userId:this.userid
			}).then(res=>{
				if(res.code=='1'){
					console.log('这里');
						_this.shopCarList=[];
						// 购物车总商品数，与总价格计算
						_this.shopCarCountClear();
						_this.hideSpec();
				}
			})
		},
		//门店详情
		storeDetail(){
			
			this.$apis.postStore({
				id:this.storeid
			}).then(res=>{
				this.storeData = res.data;
				console.log(res)
			}).catch(error=>{
				console.log(error)
			})
		},
		
		storeLocation(){
			let _this = this;
			uni.getLocation({//获取当前的位置坐标
				type: 'gcj02',
				success: function (res) {
					_this.latitude=res.latitude;
					_this.longitude=res.longitude;
				}
			});
		},
		// 获取到优惠券id回调
		UPDATA_TOKEN(data){
			// console.log('获取到优惠券id回调');
			this.couponId=data.id;
			this.getData();
		}
	},
	// 
	onLoad(option){
		if(option.aboutPickingTime){
			let tempTime = option.aboutPickingTime.split(" ");
			this.date = tempTime[0],
			this.time = tempTime[1].substring(0,tempTime[1].length-3);
		}
		this.reserveTel=option.reserveTel||uni.getStorageSync('phone')||"";
		this.couponId=option.couponId|| "",
		this.storeid=option.storeid;
		this.totalCount=option.totalCount,
		// 位置
		this.type=option.type
		this.storeLocation();
		//店铺详情
		this.storeDetail();
		//购买商品清单
		this.getData(option);
		//商家优惠券
		
		this.tokenCard();
		Event.addNoticeFun(Event.UPDATA_TOKEN, "UPDATA_TOKEN", this)
	},
	onUnload(){
			Event.removeNoticeFun(Event.UPDATA_TOKEN)
	}
}
